html {
    background-color: #282c34;
    background-image: url("assets/textures/checkeredGrunge.png");
    background-position: center;
    background-repeat: repeat;
    background-size: 218px;
}

.App {
    text-align: center;
}

.App-body {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    background: radial-gradient(transparent 40%, #000);
}

.gameStats {
    text-align: center;
    font-size: 18px;
    position: absolute;
    top: 15px;
    z-index: 160;
}

.gameStats_turns {
    color: #282c34;
    padding: 10px;
    height: 20px;
    line-height: 20px;
    border-radius: 8px;
    top: 10px;
    left: 190px;
    right: 190px;
    text-shadow: hsla(0,0%,40%,.5) 0 -1px 0, hsla(0,0%,100%,.6) 0 2px 1px;
    background: #aaa;
    box-shadow: inset hsla(0,0%,15%, 1) 0 0px 0px 4px,
        /* border */ inset hsla(0,0%,15%, .8) 0 -1px 5px 4px,
        /* soft SD */ inset hsla(0,0%,0%, .25) 0 -1px 0px 7px,
        /* bottom SD */ inset hsla(0,0%,100%,.7) 0 2px 1px 7px,
        /* top HL */ hsla(0,0%, 0%,.15) 0 -5px 6px 4px;
}



.gameStats_hearts {
    letter-spacing: 2px;
    left: 75px;
    text-shadow: hsla(0,0%,40%,.5) 0 -1px 0, hsla(0,0%,100%,.3) 0 2px 1px;
}

.gameStats_charges {
    letter-spacing: 4px;
    right: 75px;
    text-shadow: hsla(0,0%,40%,.5) 0 -1px 0, hsla(0,0%,0%,.8) 0 2px 1px, hsla(0,0%,100%,.3) 0 3px 2px;
}

.frame * { user-select: none; }
.frame {
    position: relative;
    width: 445px;
    height: 666px;
    background: url("assets/textures/gameFrame.png") top right no-repeat;
}

.viewPort {
    height: 560px;
    width: 373px;
    margin: 36px 8px 10px 8px;
    position: relative;
    overflow: hidden;
}

.viewPort:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 16px;
    box-shadow: inset 0 6px 6px 0 black;
    z-index: 20;
}

.box {
    position: absolute;
    margin-bottom: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
}


.field {
    z-index: 10;
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: column-reverse;
}

.board {
    display: flex;
    flex-direction: column-reverse;
    z-index: 1;
}
.board_section {
    display: flex;
    flex-direction: column-reverse;
}

.movementModal {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: black;
    opacity: 0.25;
    z-index: 100;
}

.row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.stage_base  {
    flex-grow: 0;
    flex-shrink: 0;
    width: 47px;
    height: 32px;
    line-height: 32px;
    margin: 0 8px 0 0;
