.cell {
    flex-grow: 0;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    line-height: 30px;
    text-align: center;
    box-sizing: border-box;
}


.board_cell {
    position: relative;
    flex-grow: 0;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    line-height: 30px;
    text-align: center;
    box-sizing: border-box;
    background: darkgray;
    border-right-color: grey;
    border-bottom-color: grey;
    color: #282c34;
    box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.25);
}


.field_cell {
    position: relative;
    flex-grow: 0;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    line-height: 30px;
    text-align: center;
    box-sizing: border-box;
    color: white;
    text-shadow:
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
}

.cell_basic {
    background-color: darkolivegreen;
    background-image: url("assets/textures/TX Tileset grass.png");
    background-position: -32px -32px;
    border-color: transparent;
}
.cell_swamp      { background-image: url("assets/textures/TX Tileset swamp.png"); }
